@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,400i,600,700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

img {
  width: 100%;
  height: auto; }

a {
  text-decoration: none; }

b,
strong {
  font-weight: bold; }

p {
  margin-top: 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 300;
  letter-spacing: -.1rem;
  margin-bottom: 2.0rem;
  margin-top: 0; }

h1 {
  font-size: 4.6rem;
  line-height: 1.2; }

h2 {
  font-size: 3.6rem;
  line-height: 1.25; }

h3 {
  font-size: 2.8rem;
  line-height: 1.3; }

h4 {
  font-size: 2.2rem;
  letter-spacing: -.08rem;
  line-height: 1.35; }

h5 {
  font-size: 1.8rem;
  letter-spacing: -.05rem;
  line-height: 1.5; }

h6 {
  font-size: 1.6rem;
  letter-spacing: 0;
  line-height: 1.4; }

.App {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-size: 1.1em;
  color: #333;
  display: flex;
  flex-direction: column;
  height: 100vh; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

.view-container {
  max-width: 1200px;
  margin: 0 auto; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.navigation {
  width: 100%;
  background: #12284c;
  position: relative;
  padding: .5em; }
  .navigation .logo-wrapper {
    width: 150px;
    top: 10px;
    left: 10px;
    position: relative; }
  .navigation ul {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    position: relative;
    top: -20px;
    right: 10px; }
  .navigation li {
    margin: 0 .5em; }
  .navigation a {
    color: white; }

.nav-content {
  max-width: 1200px;
  margin: 0 auto; }

.footer {
  background: #ff5c35;
  color: white;
  height: 50px;
  margin-top: auto; }

.footer-content {
  margin-top: 15px; }

.dashboard .logo-wrapper {
  width: 260px;
  margin: 0 auto;
  opacity: .7;
  padding-top: 1em; }
  .dashboard .logo-wrapper p {
    font-weight: bold;
    position: relative;
    top: 15px; }

.dashboard-content {
  padding-top: 3em;
  text-align: center;
  max-width: 700px;
  margin: 0 auto; }
  .dashboard-content p {
    margin: 2em 0;
    line-height: 1.2; }

.about {
  padding: 5em 1em;
  max-width: 500px;
  margin: 0 auto; }
  .about p {
    text-align: left;
    font-size: 1.1em;
    font-weight: 300; }

.cta-wrapper {
  margin-top: 1em;
  display: flex;
  flex-direction: row;
  justify-content: space-around; }
  .cta-wrapper a {
    color: #12284c;
    text-transform: uppercase;
    font-weight: bold;
    -webkit-transition: .5s;
    transition: .5s;
    font-size: 1rem; }
  .cta-wrapper a:hover {
    color: #ff5c35; }

